<template>
  <el-col :span='24' class='outerContainer'>
    <el-col :span='24' class='logoTitle'>往年高考人数及录取比例</el-col>
    <el-col :span='24' class='mainContent'>
      <el-col :span='8' class='leftData'>
        <el-col :span='24' class='tableTop'>
          <el-col :span='16' class='tabsContent'>
            <el-form ref="form" :model="form" label-width="80px">
              <el-form-item label="条件筛选">
                <el-select v-model="form.region" placeholder="请选择数据年份" @change="getTbaleData">
                  <el-option :label="select.label" :value="select.value" v-for="(select,$index) in  selectUnit" :key='$index'></el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </el-col>
        </el-col>
        <el-col :span='24' class='mainTab'>

          <el-table :data="tableData" border style="width: 100%">
            <el-table-column type="index" label="序号" align="center" width="60">
            </el-table-column>
            <el-table-column prop="date" label="年份" align="center">
            </el-table-column>
            <el-table-column prop="participate" label="参考人数(万人)" align="center">
            </el-table-column>
            <el-table-column prop="name" label="录取人数(万人)" align="center">
            </el-table-column>
            <el-table-column prop="address" label="录取率" align="center">
            </el-table-column>
            <el-table-column prop="sum" label="招生高校数" align="center">
            </el-table-column>
          </el-table>

        </el-col>
      </el-col>
      <el-col :span='10' class='mainMap'>
        <el-col :span='24' id='mainMap'></el-col>
      </el-col>
      <el-col :span='6' class='chartContent'>
        <el-col :span='24' class='mainChart'>
          <el-col :span='24' class='chartTitle'>现有数据最近年份高考录取比例<span class='light'>{{nowSelect}}</span></el-col>
          <el-col :span='24' class='echart' id='chart'></el-col>
        </el-col>
      </el-col>
    </el-col>
  </el-col>
</template>

<script>
  export default {
    name: 'home',
    data() {
      return {
        form: {
          region: ''
        },
        selectUnit: [{
            label: '全部',
            value: 'all'
          },
          {
            label: '2019年',
            value: 2019
          },
          {
            label: '2018年',
            value: 2018
          },
          {
            label: '2017年',
            value: 2017
          },
          {
            label: '2016年',
            value: 2016
          },
          {
            label: '2015年',
            value: 2015
          },
          {
            label: '2014年',
            value: 2014
          },
          {
            label: '2013年',
            value: 2013
          },
          {
            label: '2012年',
            value: 2012
          },
          {
            label: '2011年',
            value: 2011
          },
          {
            label: '2010年',
            value: 2010
          },
          {
            label: '2009年',
            value: 2009
          },
          {
            label: '2008年',
            value: 2008
          },
          {
            label: '2007年',
            value: 2007
          },
          {
            label: '2006年',
            value: 2006
          }
        ],
        normalTbaleData: [],
        tableData: [{
            date: '2019',
            participate: 1031,
            name: 820,
            address: '79.53%',
            sum: 2688
          }, {
            date: '2018',
            participate: 975,
            name: 790.99,
            address: '81.13%',
            sum: 2663
          }, {
            date: '2017',
            participate: 940,
            name: 700,
            address: '74.47%',
            sum: 2631
          }, {
            date: '2016',
            participate: 940,
            name: 705,
            address: '75%',
            sum: 2595
          },
          {
            date: '2015',
            participate: 942,
            name: 700,
            address: '74.31%',
            sum: 2553
          },
          {
            date: '2014',
            participate: 939,
            name: 697,
            address: '74.23%',
            sum: 2529
          },
          {
            date: '2013',
            participate: 912,
            name: 684,
            address: '75%',
            sum: 2622
          },
          {
            date: '2012',
            participate: 915,
            name: 685,
            address: '74.86%',
            sum: 2542
          },
          {
            date: '2011',
            participate: 933,
            name: 675,
            address: '72.35%',
            sum: 2487
          },
          {
            date: '2010',
            participate: 946,
            name: 657,
            address: '69.45%',
            sum: 2385
          },
          {
            date: '2009',
            participate: 1020,
            name: 629,
            address: '61.67%',
            sum: 2305
          },
          {
            date: '2008',
            participate: 1050,
            name: 599,
            address: '57.05%',
            sum: 2263
          },
          {
            date: '2007',
            participate: 1010,
            name: 566,
            address: '56.04%',
            sum: 1908
          },
          {
            date: '2006',
            participate: 950,
            name: 546,
            address: '57.45%',
            sum: 1867
          }
        ],
        Map: [],
        option: {

          tooltip: {
            trigger: 'item'
          },
          legend: {
            textStyle: {
              color: "#fff"
            },
          },
          series: [{
            name: '录取情况',
            type: 'pie',
            radius: '50%',
            data: [{
                value: 1048,
                name: '已录取'
              },
              {
                value: 735,
                name: '未录取'
              },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }]
        },
        nowSelect: '2019年',
        icon1: require('../../assets/point2.png'),
        clickListener: ''

      }
    },
    methods: {
      getTbaleData(val) {
        this.normalTbaleData.map(item => {
          if (val == item.date) {
            this.tableData = []
            this.tableData.push(item)
            this.nowSelect = item.date + '年'
          }
          if (val === 'all') {
            this.tableData = this.normalTbaleData
            this.nowSelect = this.tableData[0].date + '年'
          }
          this.option.series[0].data[0].value = this.tableData[0].name
          this.option.series[0].data[1].value = this.tableData[0].participate - this.tableData[0].name
          this.$initChart('chart', this.option)
        })
      },
      mapCitySearch(city, _this) {
        var polygons = [];
        AMap.plugin('AMap.DistrictSearch', function() {
          var districtSearch = new AMap.DistrictSearch({
            // 关键字对应的行政区级别，country表示国家
            level: 'district',
            //  显示下级行政区级数，1表示返回下一级行政区
            subdistrict: 0,
            extensions: "all",
          })

          // 搜索所有省/直辖市信息
          districtSearch.search(city, function(status, result) {

            // 查询成功时，result即为对应的行政区信息
            var bounds = result.districtList[0].boundaries;
            if (bounds) {
              for (var i = 0, l = bounds.length; i < l; i++) {
                //生成行政区划polygon
                var polygon = new AMap.Polygon({
                  strokeWeight: 3,
                  strokeColor: "#00F6FF",
                  path: bounds[i],
                  fillColor: '#00F6FF',
                  fillOpacity: 0.1,
                });
                polygons.push(polygon);
                _this.Map.add(polygons)
              }
            }

            _this.Map.setFitView()
          })
        })
      }
    },
    created() {
      this.normalTbaleData = this.tableData

    },
    mounted() {
      this.Map = new AMap.Map('mainMap', {
        //viewMode: '3D',
        center: [116.406315, 39.908775],
        zoom: 5,
        resizeEnable: true,
        mapStyle: 'amap://styles/normal'
      })

      let _this = this

      this.option.series[0].data[0].value = this.normalTbaleData[0].name
      this.option.series[0].data[1].value = this.normalTbaleData[0].participate - this.normalTbaleData[0].name
      this.$initChart('chart', this.option)


      AMap.event.addListener(this.Map, 'click', function(ev) {

        let random = parseInt(Math.random() * 14)
        AMap.service('AMap.Geocoder', function() { //回调函数
          //实例化Geocoder
          let geocoder = new AMap.Geocoder({
            city: "" //城市，默认：“全国”
          });
          var lnglatXY = [ev.lnglat.getLng(), ev.lnglat.getLat()]; //地图上所标点的坐标
          geocoder.getAddress(lnglatXY, function(status, result) {
            this.Map.clearMap()
            this.mapCitySearch(result.regeocode.addressComponent.province, _this)
            var text = new AMap.Text({
              text: '报考人数：' + this.normalTbaleData[random].participate + '人，录取人数：' + this.normalTbaleData[
                random].name + '人',
              anchor: 'center', // 设置文本标记锚点
              draggable: true,
              cursor: 'pointer',
              angle: 10,
              position: [ev.lnglat.getLng(), ev.lnglat.getLat()]
            });

            text.setMap(this.Map);
          }.bind(_this));
        }.bind(_this))


      })
    }
  }
</script>

<style scoped="scoped">
  .outerContainer {
    height: 100%;
    background: rgba(0, 0, 0, .4);
  }

  .logoTitle {
    height: 60px;
    line-height: 60px;
    color: #fff;
    font-size: 32px;
    text-align: center;
    margin-top: 20px;
    background: rgba(255, 255, 255, .6);
  }

  .mainContent {
    height: calc(100% - 80px);
  }

  .leftData {
    height: calc(100% - 20px);
    padding: 18px 10px 10px 10px;
    background: rgba(255, 255, 255, .2);
    margin-top: 14px;
    font-weight: bold !important;
  }


  .tabBtn {
    height: 30px;
    line-height: 30px;
    border: 1px solid skyblue;
    color: #f2f2f2;
    text-align: center;
    border-radius: 8px;
    cursor: pointer;
  }

  .mainMap {
    height: calc(100% - 40px);
    padding: 10px;
    margin-top: 20px;
  }

  #mainMap {

    height: 100%;
  }

  .chartContent {
    height: calc(100% - 20px);
    background: rgba(255, 255, 255, .2);
    margin-top: 14px;
  }

  .mainChart {
    height: 60%;
    margin-top: 180px;
    padding: 10px;
  }

  .chartTitle {
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    background: rgba(0, 0, 0, .4);
  }

  #chart {
    margin-top: 16px;
    height: calc(100% - 56px);
  }

  .light {
    font-weight: bold;
    color: skyblue;
    display: inline-block;
    margin-left: 4px;
  }

  /deep/ .amap-logo {
    display: none !important;
    visibility: hidden !important;
  }

  /deep/ .amap-copyright {
    display: none !important;
    visibility: hidden !important;
  }

  /deep/ .el-form-item__label {
    color: skyblue;
    font-weight: bold;
  }
</style>



