<template>
  <div id="app" :style='{background:src}'>
    <nav-header></nav-header>
  </div>
</template>

<script>
import navHeader from "@/components/main/home"
export default {
  name: 'App',
  data(){
   return{
      src:"url(" + require("./assets/back.jpeg") + ") no-repeat"
   }
  },
  components:{
    navHeader
  }
}
</script>

<style>
  *{
    margin: 0;
    padding: 0;
  }
  html{
    height: 100%;
  }
  body{
    height: 100%;
  }
  #app{
    height: 100%;
    background-size: cover !important;
  }
</style>



